<template>
    <div class="wrap_body_banner">
        <div class="wrap_info">
            <img :src="userInfo.avatar" class="img_nhan_vat" alt="">
            <div class="info_user">
                <div class="row_info">
                    <img src="/icon/mail.png" style="" class="image_icon" alt="">
                    <span>Email: {{ userInfo.email }}</span>
                </div>
                 <div class="row_info">
                    <img src="/icon/avatar.png" style="" class="image_icon" alt="">
                    <span>Tài khoản : <span class="color_green" style="font-weight: bold">{{ userInfo.username }}</span></span>
                </div>
                 <div class="row_info">
                    <img src="/icon/business.png" style="" class="image_icon" alt="">
                    <span>ID của bạn là : <span class="color_green" style="font-weight: bold">{{ userInfo.user_payment_code }}</span></span>
                </div>
                 <div class="row_info">
                    <img src="/icon/clock.png" style="" class="image_icon" alt="">
                    <span>Tham gia vào lúc 02/07/2020 -02:10 PM</span>
                </div>

                <div class="row_action">
                    <button type="button" class="btn_action" style="background-color:#007a4b; border : none; cursor: pointer" @click="logout()">Đăng xuất</button>
                    <button type="button" class="btn_action" @click="goToReportError($event)" style="background-color:#e5303e;border : none; cursor: pointer">Báo lỗi</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters, mapActions  } from 'vuex'

export default {
    data() {
        return {
          
        }
    },
    created() {
        this.loadInfoUser()
    },
    computed : {
        ...mapGetters({
            'userInfo' : 'user/getInfoUser',
        }),
    },
    components : {
    },
    methods : {
        goToReportError(item) {
             this.$f7router.navigate('/baoloi/')
        },
        logout() {
            localStorage.removeItem('user_token');
            this.$f7router.navigate('/')
        },
        ...mapActions({
            'loadInfoUser' : 'user/loadInfoUser',
        })
    },
}
</script>
