<template>
    <f7-page hide-toolbar-on-scroll>
        <NavbarPage></NavbarPage>
        <InfoPage></InfoPage>
        <div class="wrapper-body">
            
        <div class="wrap_body">
            <div class="wrap_title_bao_loi">
                <span style="margin-right:5px; cursor:pointer" @click="iconBack()">
                    <img src="./../../public/i_arr.png"  alt="">
                </span>
                <span>Form thông tin báo lỗi</span>
            </div>
            <div class="wrap_content_bao_loi" >
                <ValidationObserver ref="observer" v-slot="valid" tag="form" @submit.prevent="submit()">
                <div class="row_form">
                    <div class="row_label">
                        <span >Họ tên</span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider rules="required" v-slot="{ errors }">
                        <div class="row_input">
                            <input type="text" value="" 
                            v-model="user_name" 
                            class="input_form" 
                            placeholder="">
                        </div>
                        <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Email liên hệ</span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider :rules="{required: true, email: true}" v-slot="{ errors }">
                        <div class="row_input">
                            <input type="text" name="" value="" class="input_form" 
                            v-model="user_email" placeholder="">
                        </div>
                        <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                    <div style="color: #CCC; font-style: italic" class="note_email">
                        (Vui lòng điền đúng email để chúng tôi gửi kết quả hỗ trợ cho bạn)
                    </div>
                </div>
                <div class="row_form">
                    <div class="row_label">
                        <span >SĐT liên hệ</span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider :rules="{required: true, numeric: true}" v-slot="{ errors }">
                        <div class="row_input">
                            <input type="text" name="" v-model="user_phone" value="" class="input_form" placeholder="">
                        </div>
                        <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Loại lỗi </span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider rules="excluded:-1" v-slot="{ errors }">
                    <div class="row_input">
                        <select name="" v-model="type_error"  class="select_form">
                            <option value="-1">Chọn loại lỗi</option>
                            <option v-for="(item,index) in listTypeError" :key="item.id" :value="item.id">{{ item.value }}</option>
                        </select>
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Chọn game </span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider rules="excluded:-1" v-slot="{ errors }">
                    <div class="row_input">
                        <select name=""  class="select_form" v-model="game_is_play">
                            <option value="-1">Chọn game</option>
                            <option v-for="(item,index) in listGame" :key="item.game_key" :value="item.game_key">{{ item.game_display_name }}</option>
                        </select>
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Chọn server</span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider rules="excluded:-1" v-slot="{ errors }">
                    <div class="row_input">
                        <select name=""  class="select_form" v-model="server_is_play">
                            <option value="-1">Chọn server</option>
                            <option v-for="(item, index) in listServer" :key="index" :value="item.server_id">{{ item.server_name }}</option>
                        </select>
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>
                <div class="row_form">
                    <div class="row_label">
                        <span >Hình thức thanh toán</span>
                    </div>
                    <div class="row_input">
                        <input type="text" name="" v-model="payment_type" class="input_form" placeholder="">
                    </div>
                </div>
                <div class="row_form">
                    <div class="row_label">
                        <span >ID nhân vật</span>
                    </div>
                    <div class="row_input">
                        <input type="text" name="" v-model="id_figure" class="input_form" placeholder="">
                    </div>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Tên nhân vật</span>
                        <span class="color_red" > *</span>
                    </div>
                    <div class="row_input">
                        <input type="text" name="" v-model="name_figure" class="input_form" placeholder="">
                    </div>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Thời gian gặp lỗi</span>
                        <span class="color_red" > *</span>

                    </div>
                    <div class="row_input"></div>
                    <div class="row_input">
                        <!-- <input type="text" placeholder="11-03-2020" v-model="time_show" readonly="readonly" id="report_time_error" class="datepicker_report" /> -->
                        <datetime type="datetime" v-model="time_error"
                            format="dd-MM-yyyy HH:mm:ss" 
                            class="input_form date_vue"
                            :max-datetime="new Date().toISOString()"
                        ></datetime>

                    </div>
                </div>

                <div class="row_form">
                    <div class="row_label">
                        <span >Chi tiết lỗi</span>
                        <span class="color_red" > *</span>
                    </div>
                    <validation-provider :rules="{required: true}" v-slot="{ errors }">
                    <div class="row_input">
                        <textarea name="" rows="4" class="textarea_form" v-model="error_detail" placeholder="Vui lòng cung cấp những thông tin chi tiết để chúng tôi có thể hỗ trợ bạn sớm nhất"></textarea>
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>
                <div class="row_form">
                    <div class="row_label" style=" display: flex; align-items: center;">
                        <img src="/i_attach.png" alt="" style=" transform: rotate(90deg); cursor: pointer"
                         id="fileInputButton" @click="triggerFIle()"
                        >
                        <span class="color_green" style="font-weight:500; margin-left: 10px">Ảnh mô tả lỗi</span>
                    </div>
                    <input id="file_image_error" 
                        accept="image/*" 
                        type="file" 
                        style="display:none" 
                        @change="onFileChange($event)"
                        multiple
                    />
                    <span style="font-style: italic; font-weight: 500; color: #999999" class="note_cmtnd">(Bạn có thể chọn lại ảnh nếu nhầm lẫn)</span>
                </div>
                <!-- <img  v-if="image" :src="image" alt="" style=" width: auto;height: auto; max-width: 200px;max-height: 150px;"> -->
                <!-- style="flex-wrap: nowrap;white-space: nowrap;max-width: 500px;overflow: auto;" -->
                <div v-if="dataImage.length > 0" class="wrap_image_error" style="" >
                    <div v-for="(item, index) in dataImage" :key="index" style="">
                        <img :src="item"  class="img_show_error" style=""/>
                        <span style="" 
                            class="span_image_show_err" 
                            @click="removeImage(index)"
                        >X</span>
                    </div>
                </div>
                <div class="row_form" >
                    <div class="" style="display: flex; margin: auto;justify-content: center;">
                        <button class="btn_submit" type="submit">Gửi</button>
                    </div>
                </div>
                </ValidationObserver>
            </div>
        </div>
        </div>

        <!-- footer -->
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Việt Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
    </f7-page>
</template>

<script>
import NavbarPage from './navbarpage.vue';
import InfoPage from './info.vue';
import { mapGetters, mapActions  } from 'vuex'
import moment from 'moment';
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate'
import { required, email, numeric, excluded } from 'vee-validate/dist/rules';
import { Datetime } from 'vue-datetime';
import lodash from 'lodash'

extend('required', {
  ...required,
  message: 'Trường thông tin không được để trống'
});

extend('email', {
  ...email,
  message: 'Trường thông tin không hợp lệ'
});

extend('numeric', {
  ...numeric,
  message: 'Trường thông tin phải là số'
});

extend('excluded', {
  ...excluded,
  message: 'Trường thông tin chưa chọn'
});

export default {
    components : {
        NavbarPage,
        InfoPage,
        ValidationProvider,
        ValidationObserver,
        datetime : Datetime
    },
    data() {
        return {
            dataImage : [],
            dataFile : [],
        }
    },
    created() {
        this.loadGame()
    },
    watch : {
        game_is_play : function(newVal, oldVal) {
            this.loadServer(newVal)
        },
    },
    computed: {
        moment: function () {
            return moment().format("DD/MM/YYYY");
        },
        user_name: {
            get() {
                return this.$store.state.reporterror.obj.user_name;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_USER_NAME', value);
            },
        },
        user_email: {
            get() {
                return this.$store.state.reporterror.obj.user_email;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_USER_EMAIL', value);
            },
        },
        user_phone: {
            get() {
                return this.$store.state.reporterror.obj.user_phone;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_USER_PHONE', value);
            },
        },
        name_figure: {
            get() {
                return this.$store.state.reporterror.obj.name_figure;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_NAME_FIGURE', value);
            },
        },
        id_figure: {
            get() {
                return this.$store.state.reporterror.obj.id_figure;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_ID_FIGURE', value);
            },
        },
        type_error: {
            get() {
                return this.$store.state.reporterror.obj.type_error;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_TYPE_ERROR', value);
            },
        },
        game_is_play: {
            get() {
                return this.$store.state.reporterror.obj.game_is_play;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_GAME_IS_PLAY', value);
            },
        },
        server_is_play: {
            get() {
                return this.$store.state.reporterror.obj.server_is_play;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_SERVER_IS_PLAY', value);
            },
        },
        payment_type: {
            get() {
                return this.$store.state.reporterror.obj.payment_type;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_PAYMENT_TYPE', value);
            },
        },
        time_error: {
            get() {
                return this.$store.state.reporterror.obj.time_error;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_TIME_ERROR', value);
            },
        },
        error_detail: {
            get() {
                return this.$store.state.reporterror.obj.error_detail;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_ERROR_DETAIL', value);
            },
        },
        files: {
            get() {
                return this.$store.state.reporterror.obj.files;
            },
            set(value) {
                this.$store.commit('reporterror/UPDATE_FILES', value);
            },
        },
        ...mapGetters({
            'listGame' : 'list/getListGame',
            'listServer' : 'list/getListServer',
            'listTypeError' : 'list/getlistTypeError',
            'infoUser' : 'user/getInfoUser'
        })
    },
    methods: {
        initCalendar () {
            this.$f7.calendar.create({
                url: '/baoloi/',
                inputEl: '#report_time_error',
                on: {
                    change: this.setDate,
                },
                dateFormat : 'dd-mm-yyyy',
                timePicker : true,
                animate : true,
                yeadPicker : true,
                disabled: function (date) {
                    let today = moment().format("X");
                    let disabled = moment(date).format("X");
                    if (today > disabled) {
                        return false
                    } else {
                        return true
                    }
                },
                footer: true, 
            });
        },
        setDate (c, val) {
            this.$store.commit('reporterror/UPDATE_TIME_ERROR',val[0]);
            this.time_show =  moment(val[0]).format("DD-MM-YYYY")
        },
        async submit() {
            var that = this;
            const valid = await this.$refs.observer.validate()
            if (valid) {
                that.$f7.preloader.show();
                that.files = that.dataFile;
                let response =  await this.submitForm(this.$store.state.reporterror.obj)
                if (response.data.error_code != 0) {
                    that.$f7.preloader.hide();
                    this.$f7.dialog.alert(response.data.message,"Báo lỗi!")
                } else {
                    that.$f7.preloader.hide();
                    this.$f7.dialog.alert(response.data.message,"Thành công")
                    that.$refs.observer.reset()
                    that.$f7router.navigate('/ticket/list/')
                }
            } else {
                this.$f7.dialog.alert("Vui lòng nhập đầy đủ thông tin theo yêu cầu!","Lời nhắc")
            }
        },
        triggerFIle(){
            document.getElementById('file_image_error').click();
        },
        onFileChange(item) {
            var that = this;
            var files = item.target.files || item.dataTransfer.files;
            if (!files.length || files.length > 5 || parseInt(files.length + this.dataImage.length) > 5) {
                this.$f7.dialog.alert("Số ảnh báo lỗi không quá 5 ảnh","Báo lỗi!")
                return;
            }

            lodash.forEach(files, function(item, key) {
                var obj = {};
                var reader = new FileReader();
                reader.readAsDataURL(item);
                reader.onload = (e) => {
                    that.dataImage.push(e.target.result);
                };
                that.dataFile.push(item);
            });
       
        },
        iconBack() {
            this.$f7router.back()
        },
        removeImage(key) {
            this.dataImage.splice(key, 1);
            this.dataFile.splice(key, 1);
        },
        ...mapActions({
            'loadGame'  : 'list/loadGame',
            'loadServer' : 'list/loadServer',
            'submitForm' : 'reporterror/submitForm',
        })
    },
    mounted () {
        this.initCalendar()
    }
    
}
</script>

<style scoped>


</style>
