<template>
<f7-page>
    <NavbarPage></NavbarPage>
    <InfoPage></InfoPage>
    <div class="content">
        <f7-row>
            <div class="row_title  text-align-left">
                <h2 class="search-item" :title="'ID:'+ticket.id_ticket+ '- '+ticket.game_name+' - '+listError[ticket.type_error]">
                    <f7-icon f7="chevron_left_round" size="20px"></f7-icon> ID: {{ticket.id_ticket}} - {{ticket.game_name}} - {{listError[ticket.type_error]}}
                    <span v-if="ticket.status == 'Chờ xử lý'" raised class="badge pending">{{ticket.status}}</span>
                    <span v-else-if="ticket.status == 'Đang xử lý'" raised class="badge doing">{{ticket.status}}</span>
                    <span v-else-if="ticket.status == 'Đã xử lý'" raised class="badge done">{{ticket.status}}</span>
                    <span v-else raised class="badge close">{{ticket.status}}</span>
                </h2>
            </div>
        </f7-row>
        <br>
        <f7-row class="_4tdt">
            <div class="img-box">
                <img src="/anh_nhan_vat.png" alt="avt" class="img-avt" width="50" hidden="50">
            </div>
            <f7-messages class="message-box arrow-left" style="background-color: #e1e6df;">
                <div class="item-label" style="padding-bottom: 10px"><span>{{ticket.message}}</span></div>
                <div class="time-user-send"><span>
                        <p>{{ ticket.time_report}}</p>
                    </span></div>
                <br>
            </f7-messages>
        </f7-row>
        <f7-row class="_4tdt" v-for="(comment, ind) in  commentReverse" :key="ind">
            <f7-messages v-if="comment.admin_reply == 'CSKH'" class="message-box arrow-right" style="background-color: #fff;">
                <div class="item-label"><span>{{comment.mess_reply}}</span></div>
                <div class="time-user-send"><span style="float: right;">
                        <p>{{ comment.time_reply}}</p>
                    </span></div>
                <br>
            </f7-messages>
            <div v-else class="img-box">
                <img src="/anh_nhan_vat.png" alt="avt" class="img-avt" width="50" hidden="50">
            </div>
            <div v-if="comment.admin_reply == 'CSKH'" class="img-box">
                <img :src="comment.images_admin" alt="avt" class="img-avt" width="50" hidden="50">
            </div>
            <f7-messages v-else class="message-box arrow-left" style="background-color: #e1e6df;">
                <div class="item-label" style="padding-bottom: 10px"><span>{{comment.mess_reply}}</span></div>
                <div class="time-user-send"><span>
                        <p>{{ comment.time_reply}}</p>
                    </span></div>
                <br>
            </f7-messages>
        </f7-row>
        <form id="add-ticket-form" @submit.prevent="submitFormTicket">
            <f7-row class="feedback-box">
                <textarea v-model="message" ref="message_box" rows="5" cols="80" placeholder="Vui lòng nhập phản hồi tại đây.." style="padding: 10px"></textarea>
            </f7-row>

            <f7-row class="button-box">
                <button raised class="button col-50" type="submit" style="background-color: #00754a; border: #00754a">GỬI PHẢN HỒI</button>
                <f7-button raised class="button col-50" v-on:click="closeTicket" style="background-color: #d2d2d2; border: #d2d2d2">ĐÓNG YÊU CẦU</f7-button>
            </f7-row>
        </form>
    </div>
    <template>
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Việt Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
    </template>
</f7-page>
</template>

<script>
import NavbarPage from './navbar.vue';
import InfoPage from './info.vue';
import {
    mapGetters,
    mapActions
} from 'vuex';

export default {
    components: {
        NavbarPage,
        InfoPage
    },
    message: '',
    created() {
        if (typeof this.$f7route.params !== 'undefined') {
            const urlParams = new URLSearchParams(this.$f7route.params);
            const id_ticket = urlParams.get('id_ticket');
            const app_id = urlParams.get('app_id');
            localStorage.setItem("id_ticket", id_ticket);
            localStorage.setItem("app_id", app_id);
        }
        this.loadTicket();
        this.readTicket();
        this.getError();
        var check_ticket = this.ticket.status_hidden;
        if (check_ticket == 1) {
            this.redirect();
        }

    },
    computed: {
        ...mapGetters({
            'ticket': 'ticket/getTicketInfo',
            'listError': 'ticket/getListError',
        }),
        commentReverse: function (arrays) {
            return this.ticket.comment.slice().reverse();
        },
        redirect: function () {
            return this.$f7router.app.views.main.router.navigate("/ticket/rate");
        }
    },
    methods: {
        ...mapActions({
            'loadTicket': 'ticket/ticketInfo',
            'readTicket': 'ticket/updateTicket',
            'getError': 'ticket/listError',
            'addTicket': 'ticket/submitFormTicket',
            'removeTicket': 'ticket/removeTicket',
        }),
        submitFormTicket: function () {
            var msg = this.message;
            if (msg == '' | msg == undefined) {
                alert('Phản hồi không được để trống!')
            } else {
                localStorage.setItem("message", msg);
                this.addTicket();
            }
        },
        closeTicket: async function () {
            var status_confirm = confirm("Bạn có chắc muốn đóng ticket...?");
            if (status_confirm) {
                await this.removeTicket();
                this.redirect();
            }
        }
    },
    filters: {
        reverse: function (array) {
            return array.slice().reverse()
        }
    }
}
</script>

<style scoped>
body {
    background-color: #f5f6f8;
}

.content {
    margin: 0 7.8%;
}

.pending {
    background-color: #ffae00;
    color: #fff !important;
    border-radius: 30px !important;
}

.close {
    background-color: #007a4b;
    color: #fff !important;
    border-radius: 30px !important;
}

.done {
    background-color: #00c7ff;
    color: #fff !important;
    border-radius: 30px !important;
}

.doing {
    background-color: #ff4d31;
    color: #fff !important;
    border-radius: 30px !important;
}

.user-name {
    color: #007a4b;
    margin: 7.8% 0px;
}

.message-box {
    background-color: #fff;
    box-shadow: 1px 2px #dadada;
    border-radius: 20px;
    height: auto;
    color: #fff;
    position: relative;
    width: 75%;
}

.message-box p {
    margin: 0 1px 0 0;
}

.arrow-right:after {
    content: " ";
    position: absolute;
    right: -14px;
    bottom: 0px;
    border-top: 30px solid transparent;
    border-right: none;
    border-left: 30px solid #fff;
    box-shadow: 0px 2px #dadada;
}

.arrow-left:after {
    content: " ";
    position: absolute;
    left: -14px;
    bottom: 0px;
    border-top: 30px solid transparent;
    border-right: 30px solid #e1e6df;
    border-left: none;
    box-shadow: 0px 2px #dadada;
}

.item-label {
    padding: 15px 15px 0 15px;
    width: 92% !important;
}

.item-label span {
    color: #000;
    width: 92% !important;
}

.img-avt {
    display: block;
    object-fit: cover;
}

._4tdt {
    align-items: flex-end;
    display: flex;
    margin: 10px 9px 10px 8px;
    position: relative;
}

.img-box {
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}

.img-box::after {
    border-radius: 50%;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, .2);
    content: '';
    display: inline-block;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
}

.time-user-send {
    color: #00754a;
    font-size: 12px;
    float: right;
    padding: 0 15px;
}

.feedback-box {
    margin: 15px 0;
    border: 1px solid #dadada;
    border-radius: 15px;
    width: 100%;
}

.button-box {
    padding: 10px 0;
    align-items: center;
    margin-bottom: 30px !important;
}

.button-box button,
a {
    color: #fff !important;
    font-weight: bold;
}

::placeholder {
    padding-top: 1%;
    padding-left: 3.33%;
    color: #000;
}

@media only screen and (max-width: 414px) {
    .message-box {
        width: 70%;
    }
}
</style>
