import HomePage from './pages/home.vue';
import AboutPage from './pages/about.vue';
import FormPage from './pages/form.vue';
import DynamicRoutePage from './pages/dynamic-route.vue';
import NotFoundPage from './pages/not-found.vue';

import PanelLeftPage from './pages/panel-left.vue';
import PanelRightPage from './pages/panel-right.vue';
// dev
import TrangChuPage from './pages/trangchu.vue';
import ListTicketPage from './pages/list-ticket.vue';
import BaohackPage from './pages/baohack.vue';
import BaoloiPage from './pages/baoloi.vue';
import RatePage from './pages/rate.vue';
import DetailTicketPage from './pages/detail-ticket.vue';
import { BASE_URL } from './config'
import { URL_LOGIN } from './config'

function checkAuth(to, from, resolve, reject) {
    var access_token = localStorage.getItem('user_token')
    const router = this;
    if (!access_token && to.url !== '/') {
        reject();
        window.location.href = URL_LOGIN + BASE_URL
        // router.navigate('/');
        return;
    }
    resolve();
}
   
export default [
    {
        path: '/',
        component: TrangChuPage,
    },
    {
        path: '/baohack/',
        component: BaohackPage,
    },
    {
        path: '/baoloi/',
        component: BaoloiPage,
        beforeEnter : checkAuth
    },
    // default framework7
    {
        path: '/home',
        component: HomePage,
    },
    {
        path: '/panel-left/',
        component: PanelLeftPage,
    },
    {
        path: '/panel-right/',
        component: PanelRightPage,
    },
    {
        path: '/about/',
        component: AboutPage,
    },
    {
        path: '/form/',
        component: FormPage,
    },
    {
        path: '/dynamic-route/blog/:blogId/post/:postId/',
        component: DynamicRoutePage,
    },
    {
        path: '/ticket/list',
        component: ListTicketPage,
        beforeEnter : checkAuth,
    },
    {
        path: '/ticket/detail/:id_ticket/:app_id',
        component: DetailTicketPage,
        beforeEnter : checkAuth
    },
    {
        path: '/ticket/rate',
        component: RatePage,
        beforeEnter : checkAuth,
    },
    {
        path: '(.*)',
        component: NotFoundPage,
    },
];
