<template>
<f7-page>
    <NavbarPage></NavbarPage>
    <InfoPage></InfoPage>
    <div class="row">
        <form class="searchbar">
            <div class="searchbar-inner">
                <div class="searchbar-input-wrap">
                    <input placeholder="Tên game hoặc loại lỗi..." type="search" v-model="search">
                    <i class="searchbar-icon"></i>
                    <span class="input-clear-button"></span>
                </div>
                <span class="searchbar-disable-button">Cancel</span>
            </div>
        </form>
    </div>
    <div class="hr"></div>
    <div class="content" v-for="(item, index) in  filteredList" :key="index">
        <f7-row>
            <div class="row_title  text-align-left">
                <h2 class="search-item" :title="'ID:'+item.id_ticket+ '- '+item.game_name+' - '+listError[item.type_error]">
                    <f7-icon f7="chevron_left_round" size="20px"></f7-icon> ID: {{item.id_ticket}} - {{item.game_name}} - {{listError[item.type_error]}}
                </h2>
            </div>
        </f7-row>
        <f7-row>
            <div class="col-30 text-align-left">
                <f7-link v-if="item.status == 'Chờ xử lý'" class="button pending" @click="$f7router.navigate('/ticket/detail/'+item.id_ticket+'/'+item.app_id)">{{item.status}}</f7-link>
                <f7-link v-else-if="item.status == 'Đang xử lý'" class="button doing" @click="$f7router.navigate('/ticket/detail/'+item.id_ticket+'/'+item.app_id)">{{item.status}}</f7-link>
                <f7-link v-else-if="item.status == 'Đã xử lý'" class="button done" @click="$f7router.navigate('/ticket/detail/'+item.id_ticket+'/'+item.app_id)">{{item.status}}</f7-link>
                <f7-link v-else class="button close" @click="$f7router.navigate('/ticket/detail/'+item.id_ticket+'/'+item.app_id)">{{item.status}}</f7-link>
            </div>
            <div class="col-70 text-align-right">
                <p>Thời gian lỗi: {{item.time_report}}</p>
            </div>
        </f7-row>
        <div class="item-label" style="padding-bottom: 10px"><span><b class="user-name">{{item.rolename}}:</b> {{item.message}}</span></div>
        <f7-row v-for="(cmt, index) in item.comment" :key="index">
            <div v-if="cmt.admin_reply == 'CSKH' && index < 1" class="item-label" style="padding-bottom: 10px"><span><b class="user-name">{{cmt.admin_reply}}:</b> {{cmt.mess_reply}}</span></div>
        </f7-row>
    </div>
    <br>
    <template>
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Việt Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
    </template>
</f7-page>
</template>

<script>
import NavbarPage from './navbarpage.vue';
import InfoPage from './info.vue';
import {
    mapGetters,
    mapActions
} from 'vuex'
import lodash from 'lodash'

export default {
    components: {
        NavbarPage,
        InfoPage
    },
    data() {
        return {
            search: "",
        }
    },

    created() {
        this.loadTicket();
        this.getError();
    },
    computed: {
        filteredList() {
        	var str = this.search;
        	var newStr = str.normalize("NFD").replace(/[\u0300-\u036f]/g, "").replace(/đ/g, "d").replace(/Đ/g, "D");
            var that = this;
            return this.listTicket.filter(item => {
                if (newStr != '') {
                    if (!isNaN(newStr)) {
                        return item.id_ticket.includes(this.search)
                    } else {
                        return item.game_name.normalize("NFD").replace(/[\u0300-\u036f]/g, "").replace(/đ/g, "d").replace(/Đ/g, "D").toLowerCase().includes(newStr.toLowerCase()) ||
                            item.type_error_message.normalize("NFD").replace(/[\u0300-\u036f]/g, "").replace(/đ/g, "d").replace(/Đ/g, "D").toLowerCase().includes(newStr.toLowerCase())
                    }
                }
                return item
            });
        },
        listTicket: {
            get() {
                return this.$store.state.ticket.tickets;
            },
            set(value) {
                this.$store.commit('ticket/LISTTICKET', value);
            },
        },

        ...mapGetters({
            'listError': 'ticket/getListError',
        })
    },
    methods: {
        ...mapActions({
            'loadTicket': 'ticket/listTicket',
            'getError': 'ticket/listError',
        })
    },
}
</script>

<style scoped>
.hr {
    border-bottom: 1px solid #007a4b;
    margin: 0 7.8%;
}

.content {
    margin: 0 7.8%;
    border-bottom: 1px solid #ced1cd;
}

.pending {
    background-color: #ffae00;
    color: #fff !important;
    border-radius: 30px !important;
}

.close {
    background-color: #007a4b;
    color: #fff !important;
    border-radius: 30px !important;
}

.done {
    background-color: #00c7ff;
    color: #fff !important;
    border-radius: 30px !important;
}

.doing {
    background-color: #ff4d31;
    color: #fff !important;
    border-radius: 30px !important;
}

.user-name {
    color: #007a4b;
    margin: 7.8% 0px;
}
</style>
