<template>
    <div class="wrap_body">
        <div class="wrap_title">
            <span class="color_red">*</span>
            <span class="color_green">là trường thông tin bắt buộc phải điền</span>
        </div>
        <div class="wrap_content">
            <ValidationObserver ref="observer" v-slot="valid" tag="form" @submit.prevent="submit()">
            <div class="row_form">
                <div class="row_label">
                    <span >Họ tên</span>
                    <span class="color_red" > *</span>
                </div>
                <validation-provider rules="required" v-slot="{ errors }">
                    <div class="row_input">
                        <input type="text" value="" 
                        v-model="user_name" 
                        class="input_form" 
                        placeholder="">
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                </validation-provider>
            </div>

            <div class="row_form">
                <div class="row_label">
                    <span >Email liên hệ</span>
                    <span class="color_red" > *</span>
                </div>
                <validation-provider :rules="{required: true, email: true}" v-slot="{ errors }">
                    <div class="row_input">
                        <input type="text" name="" value="" class="input_form" 
                        v-model="user_email" placeholder="">
                    </div>
                    <span class="" style="color: red">{{ errors[0] }}</span>
                </validation-provider>
            </div>
            <div class="row_form">
                <div class="row_label">
                    <span >SĐT liên hệ</span>
                    <span class="color_red" > *</span>
                </div>
                <validation-provider :rules="{required: true, numeric: true}" v-slot="{ errors }">
                <div class="row_input">
                    <input type="text" name="" v-model="user_phone" value="" class="input_form" placeholder="">
                    <span class="" style="color: red">{{ errors[0] }}</span>
                </div>
                </validation-provider>

            </div>
           <!--  <div class="row_form">
                <div class="row_label">
                    <span >Thông tin tài khoản bị mất / hack</span>
                    <span class="color_red" > *</span>
                </div>
                <div class="info_login">
                    <validation-provider :rules="{required: true, numeric: true}" v-slot="{ errors }">
                        <div class="row_input">
                            <input type="text" name="" value="" class="input_form" v-model="phone_login" placeholder="SĐT đăng nhập">
                        </div>
                        <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                    <div style="margin: 5px auto"></div>
                    <validation-provider :rules="{required: true, email: true}" v-slot="{ errors }">
                        <div class="row_input">
                            <input type="text" name="" value="" v-model="email_login" class="input_form" placeholder="Email đăng nhập">
                        </div>
                        <span class="" style="color: red">{{ errors[0] }}</span>
                    </validation-provider>
                </div>
            </div> -->
            <div class="row_form">
                <div class="row_label">
                    <span >Tài khoản đăng nhập</span>
                    <span class="color_red" > *</span>
                </div>
                <validation-provider :rules="{required: true}" v-slot="{ errors }">
                <div class="row_input">
                    <input type="text" name="" v-model="account_login" value="" class="input_form" placeholder="">
                    <span class="" style="color: red">{{ errors[0] }}</span>
                </div>
                </validation-provider>

            </div>
            <div class="row_form">
                <div class="row_label">
                    <span >Game đang chơi</span>
                    <span class="color_red" > *</span>
                </div>  
                <validation-provider rules="excluded:-1" v-slot="{ errors }">
                <div class="row_input">
                    <select name="" v-model="game_is_play"  class="select_form" >
                        <option value="-1">Vui lòng chọn game</option>
                        <option v-for="(item, index) in listGame" :key="index" :value="item.game_key">{{ item.game_display_name }}</option>
                    </select>
                </div>
                <span class="" style="color: red">{{ errors[0] }}</span>
                </validation-provider>
            </div>

            <div class="row_form">
                <div class="row_label">
                    <span >Server đang chơi</span>
                </div>
                <div class="row_input">
                    <select name="" v-model="server_is_play"  class="select_form">
                        <option value="-1">Chọn server</option>
                        <option v-for="(item, index) in listServer" :key="index" :value="item.server_id">{{ item.server_name }}</option>
                    </select>
                </div>
            </div>

            <div class="row_form">
                <div class="row_label">
                    <span >Tên nhân vật</span>
                </div>
                <div class="row_input">
                    <input type="text" name="" v-model="name_figure" value="" class="input_form" placeholder="">
                </div>
            </div>

            <div class="row_form">
                <div class="row_label">
                    <span >Ghi chú</span>
                </div>
                <div class="row_input">
                    <textarea name="" rows="4" v-model="note" class="textarea_form"></textarea>
                </div>
            </div>

            <div class="row_form">
                <div class="row_label" style=" display: flex; align-items: center;">
                    <img src="/i_attach.png" alt="" style=" transform: rotate(90deg); cursor: pointer"  @click="triggerFIle()">
                    <input id="file_image_cmt" accept="image/*" type="file" style="display:none" @change="onFileChange($event)" multiple />
                    <span class="color_green" style="font-weight:500; margin-left: 10px">Ảnh CMTND đính kèm</span>
                </div>
               <!--  <div class="row_label" v-if="image">
                    <img :src="image" alt="" style=" width: auto;height: auto; max-width: 200px;max-height: 150px;">
                </div> -->
                 <div v-if="dataImage.length > 0" class="wrap_image_error" style="" >
                    <div v-for="(item, index) in dataImage" :key="index" style="">
                        <img :src="item"  class="img_show_error" style=""/>
                        <span style="" 
                            class="span_image_show_err" 
                            @click="removeImage(index)"
                        >X</span>
                    </div>
                </div>
                <div class="row_label" >
                    <span style="font-weight: 500; color: #c7c8c9; font-style: italic">(Vui lòng đính kèm hình ảnh 2 mặt CMTND và bạn có thể chọn lại ảnh nếu có nhầm lẫn.)</span>
                </div>
              <!--   <div class="row_input" style="display: flex; align-items: center">
                    <input type="checkbox" class="checkbox_form" name="" value="">
                    <span class="noti_cmtnd"> Chưa đăng ký CMTND cho tài khoản</span>
                </div> -->
            </div>

            <div class="row_form">
                <div class="row_label" style=" display: flex; align-items: center;">
                    <span class="color_green" style="font-weight:500; text-transform: uppercase;">Lịch sử giao dịch</span>
                </div>
            </div>
            
            <div class="info_history_transaction">
                <div class="row_form">
                    <div class="row_label">
                        <span >Hình thức nạp</span>
                    </div>
                    
                    <div class="row_input">
                        <select name="" v-model="payment_type_1" class="select_form">
                            <option value="-1">Hình thức nạp thẻ</option>
                            <option v-for="(item, index) in listCard" :key="index" :value="item.name">{{ item.name }}</option>
                        </select>
                    </div>
                </div>
                
                <div class="wrap_payment_option">
                    <div class="row_form">
                        <div class="row_label">
                            <span >Thời gian nạp</span>
                        </div>
                        <div class="row_input">
                           
                        </div>
                        <div class="row_input">
                            <!-- <input type="text" placeholder="11-03-2020" v-model="payment_date_1" readonly="readonly" class="datepicker_report" id="datepicker_report1" /> -->
                             <datetime type="datetime" v-model="payment_date_1"
                                format="dd-MM-yyyy HH:mm:ss" 
                                class="input_form date_vue"
                                :max-datetime="new Date().toISOString()"
                            ></datetime>
                        </div>
                    </div>
                    <div class="row_form">
                        <div class="row_label">
                            <span >Mệnh giá nạp</span>
                        </div>
                        <div class="row_input">
                            <input type="text" name=""  value="" v-model="payment_price_1" class="input_form" placeholder="VD : 10.000 VNĐ">
                        </div>
                    </div>
                </div>
            </div>

            <div class="info_history_transaction">
                <div class="row_form">
                    <div class="row_label">
                        <span >Hình thức nạp</span>
                    </div>
                    
                    <div class="row_input">
                        <select name="" v-model="payment_type_2" class="select_form">
                            <option value="-1">Hình thức nạp thẻ</option>
                            <option v-for="(item, index) in listCard" :key="index" :value="item.name">{{ item.name }}</option>
                        </select>
                    </div>
                </div>
                <div class="wrap_payment_option">
                    <div class="row_form">
                        <div class="row_label">
                            <span >Thời gian nạp</span>
                        </div>
                        <div class="row_input">
                           
                        </div>
                        <div class="row_input">
                            <!-- <input type="text" placeholder="11-03-2020" v-model="payment_date_2" readonly="readonly" id="datepicker_report2" class="datepicker_report" /> -->
                            <datetime type="datetime" v-model="payment_date_2"
                                format="dd-MM-yyyy HH:mm:ss" 
                                class="input_form date_vue"
                                :max-datetime="new Date().toISOString()"
                            ></datetime>
                        </div>
                    </div>
                    <div class="row_form">
                        <div class="row_label">
                            <span >Mệnh giá nạp</span>
                        </div>
                        <div class="row_input">
                            <input type="text" name=""  value="" v-model="payment_price_2" class="input_form" placeholder="VD : 10.000 VNĐ">
                        </div>
                    </div>
                </div>
            </div>
            <div class="info_history_transaction">
                <div class="row_form">
                    <div class="row_label">
                        <span >Hình thức nạp</span>
                    </div>
                    
                    <div class="row_input">
                        <select name="" v-model="payment_type_3" class="select_form">
                            <option value="-1">Hình thức nạp thẻ</option>
                            <option v-for="(item, index) in listCard" :key="index" :value="item.name">{{ item.name }}</option>
                        </select>
                    </div>
                </div>
                <div class="wrap_payment_option">
                    <div class="row_form">
                        <div class="row_label">
                            <span >Thời gian nạp</span>
                        </div>
                        <div class="row_input">
                           
                        </div>
                        <div class="row_input">
                            <!-- <input type="text" placeholder="11-03-2020" v-model="payment_date_3" readonly="readonly" class="datepicker_report" id="datepicker_report3" /> -->
                            <datetime type="datetime" v-model="payment_date_3"
                                format="dd-MM-yyyy HH:mm:ss" 
                                class="input_form date_vue"
                                :max-datetime="new Date().toISOString()"
                            ></datetime>
                        </div>
                    </div>
                    <div class="row_form">
                        <div class="row_label">
                            <span >Mệnh giá nạp</span>
                        </div>
                        <div class="row_input">
                            <input type="text" name=""  value="" v-model="payment_price_3" class="input_form" placeholder="VD : 10.000 VNĐ">
                        </div>
                    </div>
                </div>
                
            </div>
            <div class="row_form">
                <div class="row_label_noti">
                    <div style="font-weight:500; ">Bộ phận CSKH sẽ trao đổi chi tiết qua email hoặc số điện thoại</div>
                    <div style="font-weight:500; ">Vui lòng đăng nhập vào email của bạn để theo dõi thêm thông tin</div>
                </div>
            </div>

            <div class="row_form" >
                <div style="display: flex; margin: auto;justify-content: center;">
                    
                    <button class="btn_submit" type="submit">Xác nhận</button>
                </div>
            </div>
            </ValidationObserver>
        </div>
    </div>

</template>

<script>
import moment from 'moment';
import { mapGetters, mapActions  } from 'vuex'
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate'
import { required, email, numeric, excluded } from 'vee-validate/dist/rules';
import { Datetime } from 'vue-datetime';
import lodash from 'lodash'

extend('required', {
  ...required,
  message: 'Trường thông tin không được để trống'
});

extend('email', {
  ...email,
  message: 'Trường thông tin không hợp lệ'
});

extend('numeric', {
  ...numeric,
  message: 'Trường thông tin phải là số'
});

extend('excluded', {
  ...excluded,
  message: 'Vui lòng chọn game đang chơi'
});

export default {
    data() {
        return {
            dataImage : [],
            dataFile : [],
        }
    },
    components : {
        ValidationProvider,
        ValidationObserver,
        datetime : Datetime
    },
    created() {
        var that = this;
        this.loadGame()
    },
    watch : {
        game_is_play : function(newVal, oldVal) {
            this.loadServer(newVal)
        },
    },
    computed: {
        moment: function () {
            return moment().format("DD/MM/YYYY");
        },
        user_name: {
            get() {
                return this.$store.state.reporthack.obj.user_name;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_USER_NAME', value);
            },
        },
        user_email: {
            get() {
                return this.$store.state.reporthack.obj.user_email;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_USER_EMAIL', value);
            },
        },
        user_phone: {
            get() {
                return this.$store.state.reporthack.obj.user_phone;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_USER_PHONE', value);
            },
        },
        account_login: {
            get() {
                return this.$store.state.reporthack.obj.account_login;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_ACCOUNT_LOGIN', value);
            },
        },
        name_figure : {
            get() {
                return this.$store.state.reporthack.obj.name_figure;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_NAME_FIGURE', value);
            },
        },
        game_is_play : {
            get() {
                return this.$store.state.reporthack.obj.game_is_play;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_GAME_IS_PLAY', value);
            },
        },
        server_is_play : {
            get() {
                return this.$store.state.reporthack.obj.server_is_play;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_SERVER_IS_PLAY', value);
            },
        },
        note : {
            get() {
                return this.$store.state.reporthack.obj.note;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_NOTE', value);
            },
        },
        payment_type_1 : {
            get() {
                return this.$store.state.reporthack.obj.payment_type_1;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_TYPE_1', value);
            },
        },
        payment_date_1 : {
            get() {
                return this.$store.state.reporthack.obj.payment_date_1;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_1', value);
            },
        },
        payment_price_1 : {
            get() {
                return this.$store.state.reporthack.obj.payment_price_1;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_PRICE_1', value);
            },
        },
        payment_type_2 : {
            get() {
                return this.$store.state.reporthack.obj.payment_type_2;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_TYPE_2', value);
            },
        },
        payment_date_2 : {
            get() {
                return this.$store.state.reporthack.obj.payment_date_2;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_2', value);
            },
        },
        payment_price_2 : {
            get() {
                return this.$store.state.reporthack.obj.payment_price_2;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_PRICE_2', value);
            },
        },
        payment_type_3 : {
            get() {
                return this.$store.state.reporthack.obj.payment_type_3;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_TYPE_3', value);
            },
        },
        payment_date_3 : {
            get() {
                return this.$store.state.reporthack.obj.payment_date_3;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_3', value);
            },
        },
        payment_price_3 : {
            get() {
                return this.$store.state.reporthack.obj.payment_price_3;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_PAYMENT_PRICE_3', value);
            },
        },
        files: {
            get() {
                return this.$store.state.reporthack.obj.files;
            },
            set(value) {
                this.$store.commit('reporthack/UPDATE_FILES', value);
            },
        },
        ...mapGetters({
            'listGame' : 'list/getListGame',
            'listServer' : 'list/getListServer',
            'listCard' : 'list/getListCard',
        })

    },
    methods: {
        initCalendar() {
            this.$f7.calendar.create({
                inputEl: '#datepicker_report1',
                on: {
                    change: this.setDate1,
                },
                dateFormat : 'dd-mm-yyyy',
                animate : true,
                closeOnSelect : true,
            })

            this.$f7.calendar.create({
                inputEl: '#datepicker_report2',
                on: {
                    change: this.setDate2,
                },
                dateFormat : 'dd-mm-yyyy',
                animate : true,
                closeOnSelect : true,

            })

            this.$f7.calendar.create({
                inputEl: '#datepicker_report3',
                on: {
                    change: this.setDate3,
                },
                dateFormat : 'dd-mm-yyyy',
                animate : true,
                closeOnSelect : true,
            })
        },
        setDate1 (c, val) {
            this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_1', moment(val[0]).format("DD-MM-YYYY"));
        },
        setDate2 (c, val) {
            this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_2', moment(val[0]).format("DD-MM-YYYY"));
        },
        setDate3 (c, val) {
            this.$store.commit('reporthack/UPDATE_PAYMENT_DATE_3', moment(val[0]).format("DD-MM-YYYY"));
        },
        triggerFIle(){
            document.getElementById('file_image_cmt').click();
        },
        onFileChange(item) {
            var that = this;
            var files = item.target.files || item.dataTransfer.files;
            if (!files.length || files.length > 2 || parseInt(files.length + this.dataImage.length) > 2) {
                this.$f7.dialog.alert("Số ảnh chọn không quá 2 ảnh","Báo lỗi!")
                return;
            }

            lodash.forEach(files, function(item, key) {
                var obj = {};
                var reader = new FileReader();
                reader.readAsDataURL(item);

                reader.onload = (e) => {
                    that.dataImage.push(e.target.result);
                };
                that.dataFile.push(item);
            });
       
        },
        removeImage(key) {
            this.dataImage.splice(key, 1);
            this.dataFile.splice(key, 1);
        },
        async submit() {
            var that = this;
            that.files = that.dataFile;
            const valid = await this.$refs.observer.validate()
            if (valid) {
                that.$f7.preloader.show();
                let response = await this.submitForm(this.$store.state.reporthack.obj)
                if (response.data.error_code != 0) {
                    that.$f7.preloader.hide();
                    this.$f7.dialog.alert(response.data.message,"Báo lỗi!")
                } else {
                    that.$f7.preloader.hide();
                    this.$f7.dialog.alert(response.data.message,"Thành công")
                    that.$refs.observer.reset()
                }
            } else {
                this.$f7.dialog.alert("Vui lòng nhập đầy đủ thông tin theo yêu cầu!","Lời nhắc")
            }
        },
        ...mapActions({
            'submitForm' : 'reporthack/submitForm',
            'loadGame'  : 'list/loadGame',
            'loadServer' : 'list/loadServer'
        })

    },
    mounted () {
        this.initCalendar()
    }
    
}
</script>
