import http from '../../libs/http'
import axios from 'axios'
import moment from 'moment'

const User = {
    namespaced: true,
    state: {
        obj : {},
        listBanners : [],
    },
    getters: {
        getInfoUser : (state) => {
            return state.obj
        },
    },
    mutations: {
        UPDATE_INFO_USER(state, data) {
            state.obj = data
        },
    },
    actions: {
        async loadInfoUser({commit}) {
            let response = await http.get(`a/GET/Me/userinfo?app_id=0c812260bd20058499300db2e59bb2fe`, {
                params: {
                    access_token: localStorage.getItem("user_token"),
                }
            })
            if (response.status == 200) {
                commit("UPDATE_INFO_USER",response.data.user_info)
            } else {
                commit("UPDATE_INFO_USER",{})
            }
        },
    }
}

export default User;
