<template>
	<f7-page hide-toolbar-on-scroll>
		<Navbar></Navbar>
		<InfoPage></InfoPage>
		<div class="container">
			<form action="post" id="form-rate">
				<f7-row class="message-box">
					<p>Cảm ơn bạn đã gửi ticket cho SohaGame.<br>Vui lòng đánh giá chất lượng hỗ trợ cho chúng tôi bằng cách chọn số sao dưới đây</p>
					<div class="row rate-box" >
						<span v-for="(star, index) in stars" :key="index" :id="star.star_id" class="star" v-on:click="rate(star.star_id)" @mouseover="hover(this)" @mouseleave="hoverOut(this)" :class="{'hover-active':isHover > index, 'active':isActive > index}"></span>
						<input type="hidden" name="star-rate" :value="starValue">
					</div>
				</f7-row>

				<f7-row class="comment-box-2" v-bind:style="{display: rateVeryLow}">
					<div class="comment-title">Không hài lòng</div>
					<div class="comment-message">
						Cảm ơn bạn đã gửi đánh giá. Chúc bạn chơi game vui vẻ. Bạn có thể để lại số điện thoại tới bộ phận hỗ trợ của chúng tôi:
					</div>
					<div class="comment-checked">
						<div class="comment-textbox row">
						<textarea  name="tel_number" id="" placeholder=" Để lại số điện thoại" cols="80" rows="2"></textarea>
					</div>
					</div>
				</f7-row>

				<f7-row class="comment-box-2" v-bind:style="{display: rateLow}">
					<div class="comment-title">Bình thường</div>
					<div class="comment-message">
						Cảm ơn bạn đã gửi đánh giá. Chúc bạn chơi game vui vẻ. Bạn có thể gửi ý kiến tới bộ phận hỗ trợ của chúng tôi:
					</div>
					<div class="comment-checked">
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-time-late"><lable for="feedback-time-late">Thời gian phản hồi muộn</lable>
						</div>
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-bad"><lable for="feedback-bad">Thái độ không tốt</lable>
						</div>
						<div class="feedback-checked">
							<input type="checkbox" id="feedback-other" v-on:click="otherFeedback"><lable for="feedback-other">Ý kiến khác</lable>
						</div>
						<div class="comment-textbox row" v-bind:style="{display:checkedOther}">
							<textarea  name="other_feedback" id="" placeholder=" Để lại ý kiến" cols="80" rows="2"></textarea>
						</div>
					</div>
				</f7-row>

				<f7-row class="comment-box-1" v-bind:style="{display: rateHight}">
					<div class="comment-title">Rất hài lòng</div>
					<div class="comment-message">
						Cảm ơn bạn đã gửi đánh giá. Chúc bạn chơi game vui vẻ. Bạn có thể gửi lời khen tới bộ phận hỗ trợ của chúng tôi:
					</div>
					<div class="comment-textbox row">
						<textarea  name="good_job" id="" placeholder="Để lại lời khen" cols="80" rows="2"></textarea>
					</div>
				</f7-row>

				<button class="btn-send"><p>Gửi</p></button>
			</form>
		</div>
		<!-- footer -->
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Việt Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
	</f7-page>
</template>
<script>
import Navbar from './navbar.vue';
import InfoPage from './info.vue';

export default {
    data()  {
        return {
			isActive: false,
			isHover: false,
			rateLow: 'none',
			rateHight: 'none',
			rateVeryLow: 'none',
			checkedOther: 'none',
			starValue: 0,
			hover: function(){
				var star_id = event.target.id;
				var max_star = star_id.substring(5);
				this.isHover = max_star;
			},
			hoverOut: function(){
				this.isHover = false;
			},
			stars: [
				{star_id: 'star-1'},
				{star_id: 'star-2'},
				{star_id: 'star-3'},
				{star_id: 'star-4'},
				{star_id: 'star-5'}
			]
        }
    },
    components : {
		Navbar,
		InfoPage
    },
    methods: {
		rate: function (star_id) {
			var star = star_id.substring(5);
			// this.isActive = false;
			this.isActive = star;
			this.starValue = star;
			if(star == 5){
				this.rateVeryLow = 'none';
				this.rateLow = 'none';
				this.rateHight = 'block';
			}
			else if (star == 3 |star == 4) {
				this.rateLow = 'block';
				this.rateHight = 'none';
				this.rateVeryLow = 'none';
			}else{
				this.rateLow = 'none';
				this.rateHight = 'none';
				this.rateVeryLow = 'block';
			}
		},
		otherFeedback: function(){
			var checked = event.target.checked;
			if (checked) {
				this.checkedOther = 'block'
			}else{
				this.checkedOther = 'none'
			}
		}
	},
}

</script>

<style scoped>
	body{
		background-color: #f5f6f8;
	}
	
</style>