import http from '../../libs/http'

const Ticket = {
    namespaced: true,
    state: {
        tickets: [],
        ticketObj : {
            access_token : '',
            id_ticket : '',
            phone : '',
            email_contact : '',
            app_id : '',
            areaid : '',
            rolename : '',
            roleid : '',
            type_error : '',
            time_report : '',
            os : '',
            gver : '',
            message : '',
            type_report : 0,
            star : 0,
            feedback : '',
        },
        listError: []
    },
    getters: {
    	getListTicket: (state) => {
            return state.tickets
        },
        getTicketInfo: (state) => {
            return state.ticketObj
        },
        getListError: (state) => {
            return state.listError
        }
    },
    mutations: {
    	LISTTICKET(state, data) {
            state.tickets = data
        },
        DETAILTICKET(state, data) {
            state.ticketObj = data
        },
        LISTERROR(state, data) {
            state.listError = data
        },
        UPDATE_APP_ID(state, data) {
            state.ticketObj.app_id = data
        },
        UPDATE_AREAID(state, data) {
            state.ticketObj.areaid = data
        },
        UPDATE_ROLENAME(state, data) {
            state.ticketObj.rolename = data
        },
        UPDATE_ROLEID(state, data) {
            state.ticketObj.roleid = data
        },
        UPDATE_ACCESS_TOKEN(state, data) {
            state.ticketObj.access_token = data
        },
        UPDATE_TYPE_REPORT(state, data) {
            state.ticketObj.type_report = data
        }
    },
    actions: {
        async listTicket({commit},data) {
            var body = new FormData();
            let token = localStorage.getItem('user_token');
            body.append('access_token', token);
            let res = await http.post('a/post/report/listticket', body);
            commit("LISTTICKET", res.data.data);
        },
        async ticketInfo({commit},data) {
            var body = new FormData();
            let token = localStorage.getItem('user_token');
            let id_ticket = localStorage.getItem('id_ticket');
            body.append('access_token', token);
            body.append('id_ticket', id_ticket);
            let res = await http.post('a/post/report/ticket', body);
            commit("DETAILTICKET", res.data.data[0])
        },
        async updateTicket({commit}, data){
            var body = new FormData();
            let token = localStorage.getItem('user_token');
            let id_ticket = localStorage.getItem('id_ticket');
            let star = localStorage.getItem('star');
            let feedback = localStorage.getItem('feedback');
        	let status_read = 1;
        	body.append('access_token', token);
        	body.append('id_ticket', id_ticket);
        	body.append('status_read', status_read);
        	body.append('star', star);
        	body.append('feedback', feedback);
            let res = await http.post('a/post/report/updateticket', body);
        },
        async listError({commit},data) {
            var body = new FormData();
            let token = localStorage.getItem('user_token');
            let app_id = localStorage.getItem('app_id');
            let res = await http.get('a/get/report/info?app_id='+app_id, body);
            let error_list = res.data[1]['type_error'];
            commit("LISTERROR", error_list);
        },
        async submitFormTicket({commit},data){
        	var body = new FormData();
            let token = localStorage.getItem('user_token');
            let id_ticket = localStorage.getItem('id_ticket');
        	let message = localStorage.getItem('message');
        	body.append('access_token', token);
        	body.append('id_ticket', id_ticket);
        	body.append('message', message);
            let res = await http.post('a/post/report/addticket', body);
			location.reload();
        },
        async removeTicket({commit},data){
        	var body = new FormData();
            let token = localStorage.getItem('user_token');
            let id_ticket = localStorage.getItem('id_ticket');
        	body.append('access_token', token);
        	body.append('id_ticket', id_ticket);
            let res = await http.post('a/post/report/removeticket', body);
            if(res.status == 'success'){
                await http.post('a/post/report/updateticket', {'status_hidden': 1});
            }
        },
    }
}

export default Ticket;
