import http from '../../libs/http'
import axios from 'axios'
import moment from 'moment'
import lodash from 'lodash'

const Reporterror = {
    namespaced: true,
    state: {
        obj : {
            user_name : '',
            user_phone : '',
            user_email : '',
            type_error : '-1',
            game_is_play : '-1',
            server_is_play : '-1',
            payment_type : '',
            id_figure  : '',
            name_figure : '',
            time_error : '',
            error_detail : '',
            image_illustration : '',
            file_image : null,
            files : [],
        },
    },
    getters: {
       
    },
    mutations: {
        UPDATE_USER_NAME(state, data) {
            state.obj.user_name = data
        },
        UPDATE_USER_EMAIL(state, data) {
            state.obj.user_email = data
        },
        UPDATE_USER_PHONE(state, data) {
            state.obj.user_phone = data
        },
        UPDATE_NAME_FIGURE(state,data) {
            state.obj.name_figure = data
        },
        UPDATE_ID_FIGURE(state,data) {
            state.obj.id_figure = data
        },
        UPDATE_TYPE_ERROR(state,data) {
            state.obj.type_error = data
        },
        UPDATE_GAME_IS_PLAY(state,data) {
            state.obj.game_is_play = data
        },
        UPDATE_SERVER_IS_PLAY(state, data) {
            state.obj.server_is_play = data
        },
        UPDATE_PAYMENT_TYPE(state, data) {
            state.obj.payment_type = data
        },
        UPDATE_TIME_ERROR(state, data) {
            state.obj.time_error = data
        },
        UPDATE_ERROR_DETAIL(state,data) {
            state.obj.error_detail = data
        },
        UPDATE_IMAGE_ILLUSTRATION(state,data) {
            state.obj.image_illustration = data
        },
        UPDATE_FILE_IMAGE(state, data) {
            state.obj.file_image = data
        },
        UPDATE_FILES(state, data) {
            state.obj.files = data
        },
    },
    actions: {
        async submitForm({commit, dispatch},data) {
            var time_error_timestamp = moment(data.time_error).format("X");
            var form = new FormData();
            form.append('access_token', localStorage.getItem("user_token"));
            // form.append('access_token', 'c29hcHRva2VuMC4yMjQxNjkwMCAxNTk3MTM5ODE2KzEyNTM5NjkyMDM=');
            form.append('name', data.user_name);
            form.append('email_contact', data.user_email);
            form.append('phone', data.user_phone);
            form.append('type_error', data.type_error);
            form.append('app_id', data.game_is_play);
            form.append('areaid', data.server_is_play);
            form.append('time_report_ticket', time_error_timestamp);
            form.append('time_report', time_error_timestamp);
            form.append('rolename', data.name_figure);
            form.append('roleid', data.id_figure);
            form.append('message', data.error_detail);
            form.append('type_report', data.payment_type);
            if (data.files.length > 0) {
                var images = [];
                lodash.forEach(data.files, function(item, key) {
                    form.append('images['+key+']',item); 
                })
            }
            
            let response = await http.post(`a/post/report/addticket`, form)
            if (response.status == 200 && response.data.error_code == 0) {
                dispatch('formatFormHack')
            } 
            
            return response;
        },
        formatFormHack ({commit}) {
            commit("UPDATE_USER_NAME",' ')
            commit("UPDATE_USER_EMAIL",' ')
            commit("UPDATE_USER_PHONE",' ')
            commit("UPDATE_NAME_FIGURE",' ')
            commit("UPDATE_ID_FIGURE",' ')
            commit("UPDATE_TYPE_ERROR",'-1')
            commit("UPDATE_GAME_IS_PLAY",'-1')
            commit("UPDATE_SERVER_IS_PLAY",'-1')
            commit("UPDATE_PAYMENT_TYPE",'')
            commit("UPDATE_TIME_ERROR",'')
            commit("UPDATE_ERROR_DETAIL",'')
            commit("UPDATE_FILES",[])

        }
    }
}

export default Reporterror;
