import axios from 'axios';
import { BASE_URL } from '../../config';
import { API_BASE_URL } from '../../config';
import { API_LOCAL_URL } from '../../config';

const http = axios.create({
    baseURL: API_BASE_URL,
    // baseURL: API_LOCAL_URL,
    headers: {
        'Authorization': 'Bearer ' + (localStorage.getItem('user_token') || ''),
        'Access-Control-Allow-Methods' : 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
        'Content-type' : 'application/json',
        "access_token" :  (localStorage.getItem('user_token') || '')
    }
});
http.interceptors.request.use(function(config) {
    // Do something before request is sent
    return config;
}, function(error) {
    // Do something with request error
    // console.error(error)
    return Promise.reject(error);
});

// Add a response interceptor
http.interceptors.response.use(function(response) {
    // Do something with response data
    if (response.data.error_code == 1002) {
        let message = response.data.message;
        localStorage.removeItem('user_token');
        localStorage.setItem("expire",message);
    }
    return response;
}, function(error) {
    // Do something with response error
    let response = error.response;
    if (response != undefined) {
        if (response.status == 422) {
        	console.log(response)
            // _.forEach(response.data.errors, function(v, k) {
            //     _.forEach(v, function(value, key) {
            //         toastr.error(value);
            //     })
            // })
        }
    }
    return Promise.reject(error);
});
export default http;
