<template>
    <f7-page class="home_page" hide-toolbar-on-scroll >
        <Navbar></Navbar>
        <f7-swiper pagination navigation scrollbar>
            <f7-swiper-slide v-for="(image,index) in listBanners" :key="index">
                <img :src="image.img" height="466" width="1920" alt="" class="image_swiper_home">
            </f7-swiper-slide>
        </f7-swiper>
        
        <div class="wrapper-body">
            <!-- {{ token }} -->
            <div class="row_title_home color_text_default" >
                <div class="title1">
                     Cảm ơn bạn đã truy cập website hỗ trợ khách hàng của Sohagame.
                </div>
                <div class="title2">
                    Vui lòng lựa chọn các mục dưới đây để nhập yêu cầu hỗ trợ.
                </div>
            </div>

            <f7-list accordion-list inset>
                <f7-list-item accordion-item title="Báo mất hoặc bị hack tài khoản" class="button_accordion">
                    <f7-accordion-content>
                        <BaohackPage></BaohackPage>
                    </f7-accordion-content>
                </f7-list-item>

                <f7-list-item accordion-item title="Đăng nhập để báo lỗi" class="button_accordion" @click="handleLogin()">
                    <f7-accordion-content>
                        <!-- <About></About> -->
                    </f7-accordion-content>
                </f7-list-item>
                <f7-list-item accordion-item title="Câu hỏi và trả lời" class="button_accordion">
                    <f7-accordion-content>
                     
                    </f7-accordion-content>
                </f7-list-item>
            </f7-list>
        </div>
        
        <!-- footer -->
        <f7-toolbar bottom-md>
            <div class="tel-vn">
                Tel : 1900 6639 (Việt Nam)
            </div>
            <div class="tel-qt">
                +84-24-73 09 6868
            </div>
        </f7-toolbar>
    </f7-page>

</template>
<script>
import BaohackPage from './baohack.vue';
import Navbar from './navbarpage.vue';
import { mapGetters, mapActions  } from 'vuex'
export default {
    data() {
        return {
           
        }
    },
    components : {
        Navbar,
        BaohackPage,
    },
    created() {
        if (typeof this.$f7route.hash !== 'undefined') {
            const urlParams = new URLSearchParams(this.$f7route.hash);
            const myParam = urlParams.get('access_token');
            localStorage.setItem("user_token",myParam);
            window.location.href="/ticket/list/"
        }

        if (localStorage.getItem("expire")) {
            this.$f7.dialog.alert(localStorage.getItem("expire"),"Báo lỗi!")
            localStorage.removeItem("expire")
        }

        this.loadBanners()
    },
    computed : {
        ...mapGetters({
            'token' : 'login/getAccessToken',
            'listBanners' : 'list/getListBanners'
        })
    },
    methods : {
        handleLogin() {
            this.auth_request();
        },
        ...mapActions({
            'auth_request' : 'login/auth_request',
            'loadBanners' : 'list/loadBanners',
        })
    },
}
</script>
