import http from '../../libs/http'
import axios from 'axios'
import moment from 'moment'
import lodash from 'lodash'

const Reporthack = {
    namespaced: true,
    state: {
        obj : {
            user_name : '',
            user_phone : '',
            user_email : '',
            account_login : '',
            game_is_play : '-1',
            server_is_play : '-1',
            name_figure : '', 
            note : '',
            file_image : null,
            payment_type_1 : '-1',
            payment_price_1 : '',
            payment_date_1 : '',
            payment_type_2 : '-1',
            payment_price_2 : '',
            payment_date_2 : '',
            payment_type_3 : '-1',
            payment_price_3 : '',
            payment_date_3 : '',
            files : [],

        },
    },
    getters: {
       
    },
    mutations: {
        UPDATE_USER_NAME(state, data) {
            state.obj.user_name = data
        },
        UPDATE_USER_EMAIL(state, data) {
            state.obj.user_email = data
        },
        UPDATE_USER_PHONE(state, data) {
            state.obj.user_phone = data
        },
        UPDATE_ACCOUNT_LOGIN(state, data) {
            state.obj.account_login = data
        },
        UPDATE_NAME_FIGURE(state,data) {
            state.obj.name_figure = data
        },
        UPDATE_NOTE(state,data) {
            state.obj.note = data
        },
        UPDATE_GAME_IS_PLAY(state,data) {
            state.obj.game_is_play = data
        },
        UPDATE_SERVER_IS_PLAY(state, data) {
            state.obj.server_is_play = data
        },
        UPDATE_PAYMENT_TYPE_1(state, data) {
            state.obj.payment_type_1 = data
        },
        UPDATE_PAYMENT_DATE_1(state, data) {
            state.obj.payment_date_1 = data
        },
        UPDATE_PAYMENT_PRICE_1(state, data) {
            state.obj.payment_price_1 = data
        },
        UPDATE_PAYMENT_TYPE_2(state, data) {
            state.obj.payment_type_2 = data
        },
        UPDATE_PAYMENT_DATE_2(state, data) {
            state.obj.payment_date_2 = data
        },
        UPDATE_PAYMENT_PRICE_2(state, data) {
            state.obj.payment_price_2 = data
        },
        UPDATE_PAYMENT_TYPE_3(state, data) {
            state.obj.payment_type_3 = data
        },
        UPDATE_PAYMENT_DATE_3(state, data) {
            state.obj.payment_date_3 = data
        },
        UPDATE_PAYMENT_PRICE_3(state, data) {
            state.obj.payment_price_3 = data
        },
        UPDATE_FILE_IMAGE(state, data) {
            state.obj.file_image = data
        },
        UPDATE_FILES(state, data) {
            state.obj.files = data
        },
        
    },
    actions: {
        async submitForm({commit, dispatch},data) {
            var form = new FormData();
            var paylog = [
                {
                    paymentType : data.payment_type_1,
                    paymentPrice : data.payment_price_1,
                    paymentDate :  moment(data.payment_date_1).format("DD-MM-YYYY HH:mm:ss"),
                },
                {
                    paymentType : data.payment_type_2,
                    paymentPrice : data.payment_price_2,
                    paymentDate :  moment(data.payment_date_2).format("DD-MM-YYYY HH:mm:ss"),
                },
                {
                    paymentType : data.payment_type_3,
                    paymentPrice : data.payment_price_3,
                    paymentDate :  moment(data.payment_date_3).format("DD-MM-YYYY HH:mm:ss"),
                },
            ];
            form.append('access_token', localStorage.getItem("user_token"));
            form.append('fullname', data.user_name);
            form.append('email_contact', data.user_email);
            form.append('phone', data.user_phone);
            form.append('account_login', data.account_login);
            form.append('type_report', 4);
            form.append('message', data.note);
            form.append('registered_cmt', true);
            form.append('rolename', data.name_figure);
            form.append('app_id', data.game_is_play);
            form.append('areaid', data.server_is_play);
            form.append('payLog',JSON.stringify(paylog));
            if (data.files.length > 0) {
                var images = [];
                lodash.forEach(data.files, function(item, key) {
                    form.append('images['+key+']',item); 
                })
            }
            let response = await http.post(`a/post/report/lostaccount`, form)
            if (response.status == 200 && response.data.error_code == 0) {
                dispatch('formatFormHack')
            } 
            
            return response;
        },
        formatFormHack ({commit}) {
            commit("UPDATE_USER_NAME",' ')
            commit("UPDATE_USER_EMAIL",' ')
            commit("UPDATE_USER_PHONE",' ')
            commit("UPDATE_ACCOUNT_LOGIN",'')
            commit("UPDATE_NAME_FIGURE",'')
            commit("UPDATE_NOTE",'')
            commit("UPDATE_GAME_IS_PLAY",'-1')
            commit("UPDATE_SERVER_IS_PLAY",'-1')
            commit("UPDATE_PAYMENT_DATE_1",'')
            commit("UPDATE_PAYMENT_DATE_2",'')
            commit("UPDATE_PAYMENT_DATE_3",'')
            commit("UPDATE_PAYMENT_PRICE_1",'')
            commit("UPDATE_PAYMENT_PRICE_2",'')
            commit("UPDATE_PAYMENT_PRICE_3",'')
            commit("UPDATE_PAYMENT_TYPE_1",'')
            commit("UPDATE_PAYMENT_TYPE_2",'')
            commit("UPDATE_PAYMENT_TYPE_3",'')
            commit("UPDATE_FILES",[])
            
        }
    }
}

export default Reporthack;
